import React from "react";
import { useTransition, animated } from "react-spring";

import "../../../../index.scss";
import styles from "./CertificateDesign.module.scss";

function CertificateDesign({ certificateDesign }) {
  const transition = useTransition(certificateDesign, {
    from: { opacity: 0, transform: "translateY(-100%)" },
    enter: { opacity: 1, transform: "translateY(0)" },
  });
  const [picBig, setPicBig] = React.useState(false);

  return (
    <>
      {transition(
        (style, item) =>
          item && (
            <animated.div style={style}>
              <div
                className={
                  !picBig
                    ? `${styles.giftCardPreview}`
                    : `${styles.giftCardPreview} + ${styles.giftCardPreviewBig}`
                }
              >
                <img
                  src="https://kvadro.ml/media/postcards/preview/postcard_1/service_type_105/default.jpg"
                  loading="lazy"
                  alt=""
                ></img>
              </div>
              <div className="container">
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={() => setPicBig(!picBig)}
                >
                  {!picBig ? "Увеличить картинку" : "Уменьшить картинку"}
                </button>
              </div>
              <div className={styles.programItem}>
                <div className="container">
                  <p>Открытка придёт в 2х форматах</p>
                </div>
                <div className="container_no-top">
                  <div className="detail-text">
                    <div className={styles.imageInfoItem}>
                      <div className={styles.imageInfoItem}>
                        <img
                          width={45}
                          src="https://book-pay.ru/store/widget_6/images/icon_jpeg.png"
                          alt="JPEG ICON"
                        ></img>
                      </div>
                      <div className={styles.imageInfoText}>
                        <div className="title">JPEG</div>
                        <p>Для соц. сетей и мессенджеров</p>
                      </div>
                    </div>
                    <div className={styles.imageInfoItem}>
                      <div className={styles.imageInfoItem}>
                        <img
                          width={45}
                          src="https://book-pay.ru/store/widget_6/images/icon_pdf.png"
                          alt="PDF ICON"
                        ></img>
                      </div>
                      <div className={styles.imageInfoText}>
                        <div className="title">PDF</div>
                        <p>Распечатать и вручить лично. Формат A4</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </animated.div>
          )
      )}
    </>
  );
}
export default CertificateDesign;
