import React from "react";
import { useTransition, animated } from "react-spring";

import "../../../../index.scss";
import AppContext from "../../../../context";
import CertificateDesign from "../certificateDesign";

function Congratulation() {
  const {
    reservInfo,
    certificateDesign,
    setCertificateDesign,
    setCongratulation,
  } = React.useContext(AppContext);

  const [warning, setWarning] = React.useState(false);
  const transition = useTransition(warning, {
    from: { opacity: 0, transform: "translateX(-100%)" },
    enter: { opacity: 1, transform: "translateX(0)" },
  });

  return (
    <>
      {certificateDesign && (
        <CertificateDesign certificateDesign={certificateDesign} />
      )}
      {!certificateDesign && (
        <>
          <div className="container_x">
            <textarea
              className="felicitation__input"
              aria-disabled
              cols={30}
              rows={30}
              maxLength={300}
              value={reservInfo.congratulation}
              onChange={(e) => {
                setCongratulation(e.target.value);
                if (e.target.value.length === 300) {
                  setWarning(true);
                } else {
                  setWarning(false);
                }
              }}
              placeholder="Введите поздравление"
            ></textarea>
            {transition(
              (style, item) =>
                item && (
                  <animated.div
                    className="felicitation__input-result"
                    style={style}
                  >
                    <div className="felicitation__input-result__header">
                      Превышен лимит!
                    </div>
                    <div className="felicitation__input-result__body">
                      Сократите поздравление.
                    </div>
                    <div className="felicitation__input-result__footer">
                      Это нужно, чтобы оно хорошо смотрелось на сертификате
                    </div>
                  </animated.div>
                )
            )}
          </div>
          <div className="container">
            <div className="detail-text">
              <p>Поздравление необязательно, но человеку будет приятнее ❤</p>
            </div>
            <button
              className="btn btn-link btn-link_left"
              onClick={() => setCertificateDesign(!certificateDesign)}
            >
              Как выглядит открытка?
            </button>
          </div>
        </>
      )}
    </>
  );
}
export default Congratulation;
