import { useContext } from "react";
import AppContext from "../../../context";
import styles from "./Top.module.scss";
import "../../../index.scss";

function Top() {
  const {
    setModal,
    step,
    setStep,
    contactMap,
    setContactMap,
    certificateDesign,
    setCertificateDesign,
    faqScreen,
    setFaqScreen,
  } = useContext(AppContext);

  return (
    <div
      className={
        !contactMap
          ? step === "selfMap"
            ? !faqScreen && `${styles.popupTop} ${styles.popupTopTransparent}`
            : `${styles.popupTop}`
          : `${styles.popupTop} ${styles.popupTopTransparent}`
      }
    >
      <div className="container">
        <div className={styles.popupAction}>
          {faqScreen ? (
            <button
              type="button"
              className={styles.popupBack}
              onClick={() => {
                if (step !== "") {
                  setFaqScreen(false);
                } else {
                  setStep("");
                  setModal(false);
                  setFaqScreen(false);
                }
              }}
            >
              {step !== "" && (
                <svg
                  width="17"
                  height="12"
                  viewBox="0 0 17 12"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                    stroke="black"
                  ></path>
                </svg>
              )}
            </button>
          ) : (
            <>
              {step !== "selfTimes" ? (
                <div className={styles.popupBack}>
                  {step === "selfPrograms" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => {
                        setStep("selfTimes");
                      }}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "giftPrograms" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => {
                        setStep("giftCertificateOrder");
                      }}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "selfMap" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => setStep("selfPrograms")}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "selfVariants" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => setStep("selfMap")}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "giftVariants" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => setStep("giftPrograms")}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "selfRules" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => setStep("selfContact")}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "giftCongratulation" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => {
                        if (certificateDesign) {
                          setCertificateDesign(false);
                        } else setStep("giftVariants");
                      }}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "selfContact" &&
                    (contactMap ? (
                      <button
                        type="button"
                        className={styles.popupBack}
                        onClick={() => {
                          setStep("selfContact");
                          setContactMap(false);
                        }}
                      >
                        <svg
                          width="17"
                          height="12"
                          viewBox="0 0 17 12"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                            stroke="black"
                          ></path>
                        </svg>
                      </button>
                    ) : (
                      <button
                        type="button"
                        className={styles.popupBack}
                        onClick={() => {
                          setStep("selfVariants");
                          setContactMap(false);
                        }}
                      >
                        <svg
                          width="17"
                          height="12"
                          viewBox="0 0 17 12"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                            stroke="black"
                          ></path>
                        </svg>
                      </button>
                    ))}
                  {step === "giftCertificateOrder" && certificateDesign && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => {
                        setCertificateDesign(!certificateDesign);
                      }}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                  {step === "giftContact" && (
                    <button
                      type="button"
                      className={styles.popupBack}
                      onClick={() => {
                        if (contactMap) {
                          setContactMap(false);
                        } else setStep("giftCongratulation");
                      }}
                    >
                      <svg
                        width="17"
                        height="12"
                        viewBox="0 0 17 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M17 6H1M1 6L5.64516 11M1 6L5.64516 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  )}
                </div>
              ) : (
                <div className={styles.popupBack}></div>
              )}
            </>
          )}
          <div className={styles.popupSteps}>
            <div className={styles.popupStepList}>
              {step === "selfTimes" && (
                <>
                  <div className={styles.popupStepItem} />
                  <div className={styles.popupStepItem} />
                  <div className={styles.popupStepItem} />
                </>
              )}
              {step === "giftCertificateOrder" && !certificateDesign && (
                <>
                  <div className={styles.popupStepItem} />
                  <div className={styles.popupStepItem} />
                  <div className={styles.popupStepItem} />
                </>
              )}
              {step === "selfPrograms" && (
                <>
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div className={styles.popupStepItem} />
                  <div className={styles.popupStepItem} />
                </>
              )}
              {step === "giftPrograms" && (
                <>
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div className={styles.popupStepItem} />
                  <div className={styles.popupStepItem} />
                </>
              )}
              {step === "selfMap" && (
                <>
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div className={styles.popupStepItem} />
                </>
              )}
              {step === "selfVariants" && (
                <>
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div className={styles.popupStepItem} />
                </>
              )}
              {step === "giftVariants" && (
                <>
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div className={styles.popupStepItem} />
                  <div className={styles.popupStepItem} />
                </>
              )}
              {step === "selfContact" && !contactMap && (
                <>
                  {" "}
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                </>
              )}
              {step === "giftContact" && !contactMap && (
                <>
                  {" "}
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                </>
              )}
              {step === "selfRules" && (
                <>
                  {" "}
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                </>
              )}
              {step === "giftCongratulation" && !certificateDesign && (
                <>
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div
                    className={`${styles.popupStepItem} ${styles.popupStepItemActive}`}
                  />
                  <div className={styles.popupStepItem} />
                </>
              )}
            </div>
          </div>
          {faqScreen ? (
            <button
              type="button"
              className={styles.popupClose}
              onClick={() => {
                if (step.length > 0) {
                  setFaqScreen(false);
                } else {
                  setStep("");
                  setModal(false);
                  setFaqScreen(false);
                }
              }}
            >
              <svg
                width={14}
                height={14}
                viewBox="0 0 14 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M13.5 1L7.25 7.25M1 13.5L7.25 7.25M7.25 7.25L13.5 13.5L1 1"
                  stroke="black"
                ></path>
              </svg>
            </button>
          ) : (
            <>
              {step === "selfContact" ? (
                contactMap ? (
                  <div></div>
                ) : (
                  <button
                    type="button"
                    className={styles.popupClose}
                    onClick={() => {
                      setModal(false);
                      setStep(1);
                    }}
                  >
                    <svg
                      width={14}
                      height={14}
                      viewBox="0 0 14 14"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M13.5 1L7.25 7.25M1 13.5L7.25 7.25M7.25 7.25L13.5 13.5L1 1"
                        stroke="black"
                      ></path>
                    </svg>
                  </button>
                )
              ) : step === "giftCertificateOrder" ? (
                !certificateDesign && (
                  <>
                    <button
                      type="button"
                      className={styles.popupClose}
                      onClick={() => {
                        setModal(false);
                        setStep(1);
                      }}
                    >
                      <svg
                        width={14}
                        height={14}
                        viewBox="0 0 14 14"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M13.5 1L7.25 7.25M1 13.5L7.25 7.25M7.25 7.25L13.5 13.5L1 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  </>
                )
              ) : step === "giftCongratulation" ? (
                !certificateDesign && (
                  <>
                    <button
                      type="button"
                      className={styles.popupClose}
                      onClick={() => {
                        setModal(false);
                        setStep(1);
                      }}
                    >
                      <svg
                        width={14}
                        height={14}
                        viewBox="0 0 14 14"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M13.5 1L7.25 7.25M1 13.5L7.25 7.25M7.25 7.25L13.5 13.5L1 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  </>
                )
              ) : step === "giftContact" ? (
                !contactMap && (
                  <>
                    <button
                      type="button"
                      className={styles.popupClose}
                      onClick={() => {
                        setModal(false);
                        setStep("");
                        setFaqScreen(false);
                      }}
                    >
                      <svg
                        width={14}
                        height={14}
                        viewBox="0 0 14 14"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M13.5 1L7.25 7.25M1 13.5L7.25 7.25M7.25 7.25L13.5 13.5L1 1"
                          stroke="black"
                        ></path>
                      </svg>
                    </button>
                  </>
                )
              ) : (
                <button
                  type="button"
                  className={styles.popupClose}
                  onClick={() => {
                    setModal(false);
                    setStep("");
                    setFaqScreen(false);
                  }}
                >
                  <svg
                    width={14}
                    height={14}
                    viewBox="0 0 14 14"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M13.5 1L7.25 7.25M1 13.5L7.25 7.25M7.25 7.25L13.5 13.5L1 1"
                      stroke="black"
                    ></path>
                  </svg>
                </button>
              )}
            </>
          )}
        </div>
        <div className={styles.popupTitle}>
          {faqScreen ? (
            "Вопросы и ответы"
          ) : (
            <>
              {step === "selfTimes" && `Выберите удобное время`}
              {step === "selfPrograms" && `Выберите программу`}
              {step === "selfMap" && `Адрес проведения`}
              {step === "selfVariants" && `Выберите квадроциклы`}
              {step === "selfContact" && `Внесите контакты`}
              {step === "selfRules" && `Ознакомьтесь с ограничениями`}
              {step === "giftCertificateOrder" &&
                (certificateDesign
                  ? "Дизайн подарочной открытки"
                  : "Заказ подарочного сертификата")}
              {step === "giftPrograms" && `Выберите программу`}
              {step === "giftVariants" && `Выберите квадроциклы`}
              {step === "giftCongratulation" &&
                (certificateDesign
                  ? "Заказ подарочного сертификата"
                  : "Добавьте поздравление")}
              {step === "giftContact" && `Внесите контакты`}
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default Top;
