import { useContext } from "react";
import AppContext from "../../../context";
import "../../../index.scss";
import styles from "./Bottom.module.scss";

function Bottom() {
  const {
    reservInfo,
    step,
    setStep,
    total,
    contactMap,
    setFaqScreen,
    clientAgree,
  } = useContext(AppContext);

  var totalFormated = Intl.NumberFormat("ru-RU").format(total);
  var totalPrepayment = Intl.NumberFormat("ru-RU").format(total * 0.98);
  var totalSurcharge = Intl.NumberFormat("ru-RU").format(total * 0.02);

  return (
    <>
      {!contactMap && (
        <div className={styles.popupBottom}>
          <div className="container_x">
            <>
              {step === "selfMap" && (
                <div
                  className="addr_helper"
                  style={{ padding: "1rem 0", textAlign: "center" }}
                >
                  Нажмите на{" "}
                  <img
                    className="addr_helper-img"
                    src="https://book-pay.ru/store/widget_6/images/map/mark.svg"
                    alt="mark"
                    style={{ height: "2rem" }}
                  ></img>
                  , чтобы выбрать адрес
                </div>
              )}
              {step === "selfVariants" && (
                <div
                  className="payment-info"
                  style={{ display: "grid", gap: "8px", padding: "1rem 0" }}
                >
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                      fontSize: "1.6rem",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Стоимость:
                    </div>
                    <div
                      className="payment-info__price payment-info__price_bold"
                      style={{ fontWeight: 700 }}
                    >
                      <span className="total_price">{totalFormated}</span>{" "}
                      рублей
                    </div>
                  </div>
                </div>
              )}
              {step === "giftVariants" && (
                <div
                  className="payment-info"
                  style={{ display: "grid", gap: "8px", padding: "1rem 0" }}
                >
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                      fontSize: "1.6rem",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Стоимость:
                    </div>
                    <div
                      className="payment-info__price payment-info__price_bold"
                      style={{ fontWeight: 700 }}
                    >
                      <span className="total_price">{totalFormated}</span>{" "}
                      рублей
                    </div>
                  </div>
                </div>
              )}
              {step === "selfContact" && (
                <div
                  className="payment-info"
                  style={{ display: "grid", gap: "8px", padding: "1rem 0" }}
                >
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                      fontSize: "1.6rem",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Предоплата:
                    </div>
                    <div
                      className="payment-info__price payment-info__price_bold"
                      style={{ fontWeight: 700 }}
                    >
                      <span className="total_price">{totalSurcharge}</span>{" "}
                      рублей
                    </div>
                  </div>
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Стоимость:
                    </div>
                    <div className="payment-info__price payment-info__price_bold">
                      <span className="total_price">{totalFormated}</span>{" "}
                      рублей
                    </div>
                  </div>
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Доплата на месте:
                    </div>
                    <div className="payment-info__price payment-info__price_bold">
                      <span className="total_price">{totalPrepayment}</span>{" "}
                      рублей
                    </div>
                  </div>
                </div>
              )}
              {step === "selfRules" && (
                <div
                  className="payment-info"
                  style={{ display: "grid", gap: "8px", padding: "1rem 0" }}
                >
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                      fontSize: "1.6rem",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Предоплата:
                    </div>
                    <div
                      className="payment-info__price payment-info__price_bold"
                      style={{ fontWeight: 700 }}
                    >
                      <span className="total_price">{totalSurcharge}</span>{" "}
                      рублей
                    </div>
                  </div>
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Стоимость:
                    </div>
                    <div className="payment-info__price payment-info__price_bold">
                      <span className="total_price">{totalFormated}</span>{" "}
                      рублей
                    </div>
                  </div>
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Доплата на месте:
                    </div>
                    <div className="payment-info__price payment-info__price_bold">
                      <span className="total_price">{totalPrepayment}</span>{" "}
                      рублей
                    </div>
                  </div>
                </div>
              )}
              {step === "selfVariants" && (
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={() => {
                    if (step === "selfVariants") {
                      if (total === 0) {
                        alert("Выберите программу!");
                      } else {
                        setStep("selfContact");
                      }
                    }
                  }}
                >
                  Далее
                </button>
              )}
              {step === "selfContact" && (
                <button type="submit" className="btn btn-primary">
                  Далее
                </button>
              )}
              {step === "selfRules" && (
                <button
                  className={`btn btn-primary ${
                    !clientAgree && "btn-disabled"
                  }`}
                  onClick={() => {
                    if (step === "selfContact") {
                      if (reservInfo.clientPhone > 0) {
                        setStep("selfRules");
                      } else if (reservInfo.clientWhatsApp > 0) {
                        setStep("selfRules");
                      } else {
                        alert("Укажите телефон для связи");
                      }
                    }
                  }}
                >
                  Перейти к оплате
                </button>
              )}
              {step === "giftCertificateOrder" && (
                <button
                  className="btn btn-primary"
                  onClick={() => {
                    setStep("giftPrograms");
                  }}
                >
                  Перейти к оформлению
                </button>
              )}
              {step === "giftVariants" && (
                <button
                  type="button"
                  className="btn btn-primary"
                  onClick={() => {
                    if (step === "giftVariants") {
                      if (total === 0) {
                        alert("Выберите программу!");
                      } else {
                        setStep("giftCongratulation");
                      }
                    }
                  }}
                >
                  Далее
                </button>
              )}
              {step === "giftCongratulation" && (
                <button
                  className="btn btn-primary"
                  onClick={() => {
                    setStep("giftContact");
                  }}
                >
                  Далее
                </button>
              )}
              {step === "giftContact" && (
                <div
                  className="payment-info"
                  style={{ display: "grid", gap: "8px", padding: "1rem 0" }}
                >
                  <div
                    className="payment-info__wrapper"
                    style={{
                      display: "flex",
                      justifyContent: "space-between",
                      flexWrap: "wrap",
                      flexDirection: "row",
                      fontSize: "1.6rem",
                    }}
                  >
                    <div className="payment-info__name payment-info__name_big">
                      Стоимость:
                    </div>
                    <div
                      className="payment-info__price payment-info__price_bold"
                      style={{ fontWeight: 700 }}
                    >
                      <span className="total_price">{totalFormated}</span>{" "}
                      рублей
                    </div>
                  </div>
                </div>
              )}
              {step === "giftContact" && (
                <>
                  <span>
                    Нажимая кнопку "Перейти к оплате", я соглашаюсь с{" "}
                    <a
                      style={{
                        color: "-webkit-link",
                        cursor: "pointer",
                      }}
                      href="https://book-pay.ru/offer"
                      rel="noreferrer"
                      target="_blank"
                    >
                      условиями сервиса
                    </a>
                  </span>
                  <button type="submit" className="btn btn-primary">
                    Перейти к оплате
                  </button>
                </>
              )}
              <button
                className="btn btn-secondary"
                type="button"
                onClick={() => setFaqScreen(true)}
              >
                Вопросы и ответы
              </button>
            </>
          </div>
        </div>
      )}
    </>
  );
}

export default Bottom;
