import React from "react";
import { useTransition, animated } from "react-spring";

import "../../../../index.scss";
import AppContext from "../../../../context";
import data from "../../../../JSON/service_program.json";

function FAQ() {
  const { step, faqScreen } = React.useContext(AppContext);
  const transition = useTransition(faqScreen, {
    from: { opacity: 0, transform: "translateY(-100%)" },
    enter: { opacity: 1, transform: "translateY(0)" },
  });

  return (
    <>
      {transition(
        (style, item) =>
          item && (
            <animated.div style={style} className="container_x">
              {step !== "" ? (
                <>
                  {data.map((item) => {
                    return (
                      <details className="faq__item">
                        <summary className="btn btn-link_left faq__question">
                          <div className="btn btn_fit btn-link btn-link_left btn-link_blue faq__question_counter">
                            {item.TITLE}
                          </div>
                        </summary>
                        <div className="faq-answer">{item.TEXT}</div>
                      </details>
                    );
                  })}
                </>
              ) : (
                <>
                  {data.map((item) => {
                    return (
                      <details className="faq__item">
                        <summary className="btn btn-link_left faq__question">
                          <div className="btn btn_fit btn-link btn-link_left btn-link_blue faq__question_counter">
                            {item.TITLE}
                          </div>
                        </summary>
                        <div className="faq-answer">{item.TEXT}</div>
                      </details>
                    );
                  })}
                </>
              )}
            </animated.div>
          )
      )}
    </>
  );
}
export default FAQ;
