import React, { useContext } from "react";

import AppContext from "../../../../context";
import styles from "./Programs.module.scss";
import "./Programs.module.scss";
import "../../../../index.scss";
import data from "../../../../JSON/program.json";
import Content from "./Content";

function Programs() {
  const { setProgramInfo, step, setStep } = useContext(AppContext);

  return (
    <div className={styles.programList}>
      {data.map((item) => {
        return (
          <div key={item.id} className={styles.programItem}>
            <div className="container">
              <div className={styles.programItemTop}>
                <div className={styles.programItemTitle}>
                  {item.name} , {item.time} {item.time < 2 && "час"}
                  {item.time > 1 && "часа"}
                  {item.time > 4 && "часов"}
                </div>
                <div className={styles.programItemPrice}>
                  От {item.priceFrom} рублей
                </div>
              </div>
              <Content styles={styles} item={item} />
              <button
                className="btn btn-primary"
                onClick={() => {
                  setProgramInfo({
                    programName: item.name,
                    programTime: item.time,
                  });
                  if (step === "giftPrograms") {
                    setStep("giftVariants");
                  } else setStep("selfMap");
                }}
              >
                Выбрать
              </button>
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default Programs;
