import React from "react";

import "../../../../index.scss";
import styles from "./Rules.module.scss";
import AppContext from "../../../../context";
import data from "../../../../JSON/rules.json";

function Rules() {
  const { clientAgree, setClientAgree } = React.useContext(AppContext);

  return (
    <div className="container_no-top">
      <div>
        <div className={styles.detailText}>
          <p>
            Подтверждения записи придёт на ваш телефон. Дождитесь его и
            приезжайте в согласованное время.
          </p>
          <p>
            Отменить или перенести бронирование возможно не позднее, чем за
            сутки (24 часа), иначе бронь аннулируется и предоплата не
            возвращается. Потребуется новое оформление брони.
          </p>
          <p>
            Пожалуйста, не опаздывайте, будет жаль сокращать продолжительность
            впечатления.
          </p>
          {data.map((item) => {
            return (
              <p>
                <b>{item.TITLE}</b> {item.TEXT}
              </p>
            );
          })}
        </div>
        <div class="checkbox checkbox_mini checkbox__read-all">
          <button
            type="button"
            className={clientAgree && "buttonActive"}
            onClick={() => setClientAgree(!clientAgree)}
          >
            {clientAgree && (
              <img
                src="https://book-pay.ru/store/widget_6/images/check.svg"
                alt="check svg"
              ></img>
            )}
          </button>
          <span>
            Я всё прочитал и согласен со всеми условиями и
            <a href="/offer" target="_blank">
              правилами
            </a>
          </span>
        </div>
      </div>
    </div>
  );
}

export default Rules;
