import React from "react";

import styles from "./Variants.module.scss";
import "./inputStyle.scss";
import "../../../../index.scss";
import AppContext from "../../../../context";

function VariantItem({ obj, max, min, quantity = 0 }) {
  const { variants, setVariants } = React.useContext(AppContext);
  const [value, setValue] = React.useState(0);
  React.useEffect(() => {
    setValue(quantity);
  }, [quantity]);
  React.useEffect(() => {
    setVariants(
      variants.map((el) => {
        if (el === undefined) {
          return false;
        } else {
          if (String(el.id) === String(obj.id)) {
            if (Number(el.quantity) <= Number(obj.max)) {
              return {
                id: el.id,
                price: el.price,
                quantity: value,
                title: el.title,
                src: el.src,
                min: el.min,
                max: el.max,
              };
            } else {
              return {
                id: el.id,
                price: el.price,
                quantity: el.max,
                title: el.title,
                src: el.src,
                min: el.min,
                max: el.max,
              };
            }
          } else {
            return el;
          }
        }
      })
    );
    if (value > max) {
      setValue(max);
    }
  }, [value]);

  return (
    <div className="input-group">
      <button
        type="button"
        onClick={() => {
          if (value !== 0) {
            setValue(value - 1);
          }
        }}
      >
        -
      </button>
      <input
        className="form-control"
        inputMode="decimal"
        type="tel"
        value={value}
        onChange={(e) => setValue(value)}
      />
      <button type="button" onClick={() => setValue(value + 1)}>
        +
      </button>
    </div>
  );
}

function Variants() {
  const { setTotal, variants } = React.useContext(AppContext);
  React.useEffect(() => {
    setTotal(
      variants.reduce((total, item) => {
        var sum = total + item.price * item.quantity;
        return sum;
      }, 0)
    );
  });
  return (
    <div className="container_no-top">
      <div className={styles.variantList}>
        {variants.map((obj) => {
          return (
            <div className={styles.variantItem} key={obj.id}>
              {obj.src !== "" && (
                <div className={styles.variantImage}>
                  <img src={obj.src} alt="VARIANT IMG"></img>
                </div>
              )}
              <div key={obj.value}>
                <div className={styles.variantTitle}>{obj.title}</div>
                <div className={styles.variantInput}>
                  <div>{obj.price} рублей</div>
                  <VariantItem
                    max={obj.max}
                    min={obj.min}
                    obj={obj}
                    quantity={obj.quantity}
                  />
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}

export default Variants;
