import { useContext } from "react";

import AppContext from "../../../../context";
import "../../../../index.scss";
import styles from "./Times.module.scss";
import data from "../../../../JSON/time.json";

function Times() {
  const { setDateInfo, setStep } = useContext(AppContext);

  return (
    <div className="container_no-top">
      {data.map((item) => {
        return (
          <div key={item.date} className={styles.dateItem}>
            <div className={styles.dateTitle}>
              <div className={styles.dateWeekday}>{item.dayOfWeek}</div>
              <div className={styles.dateDay}>{item.day}</div>
            </div>
            <div className={styles.dateTimeList}>
              {item.hours.map((time) => {
                return (
                  <span key={time.hour}>
                    {time.disabled ? (
                      <button
                        className={`${styles.dateTime} ${styles.dateTimeDisabled}`}
                      >
                        {time.hour}
                      </button>
                    ) : (
                      <button
                        className={styles.dateTime}
                        onClick={() => {
                          setDateInfo({
                            time: time.hour,
                            date: item.date,
                            dayOfWeek: item.dayOfWeek,
                          });
                          setStep("selfPrograms");
                        }}
                      >
                        {time.hour}
                      </button>
                    )}
                  </span>
                );
              })}
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default Times;
