import { useContext } from "react";
import "./MapComponent.scss";
import { Map, Marker, MarkerLayout, BalloonLayout } from "yandex-map-react";
import data from "../../../../JSON/map.json";
import AppContext from "../../../../context";

function MapComponent() {
  const { setStep, setAddressInfo, screen } = useContext(AppContext);

  console.log(screen);
  return (
    <div className="map__container map-contact__container">
      <Map
        width={screen >= 500 ? 340 : screen}
        height="100%"
        center={[56.021596, 37.643]}
        zoom={15}
      >
        {data.map((obj) => {
          return (
            <Marker lat={obj.LAT} lon={obj.LON}>
              <MarkerLayout>
                <div>
                  <img
                    className="placemark--img"
                    src="https://book-pay.ru/store/widget_6/images/map/mark.svg"
                    alt="Marker"
                  ></img>
                </div>
              </MarkerLayout>
              <BalloonLayout>
                <div className="placemark">
                  <div className="placemark__inner">
                    <div className="placemark__container">
                      <div className="placemark__top">
                        <div className="placemark__title">{obj.INFO}</div>
                      </div>
                      <div className="placemark__bottom">
                        <button
                          className="btn btn-primary"
                          onClick={() => {
                            setStep("selfVariants");
                            setAddressInfo({
                              addressOfHolding: obj.ADDRESS_TT,
                              addressLAT: obj.LAT,
                              addressLON: obj.LON,
                            });
                          }}
                        >
                          Выбрать этот адрес
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className="placemark__tail"></div>
                </div>
              </BalloonLayout>
            </Marker>
          );
        })}
      </Map>
    </div>
  );
}
export default MapComponent;
