import React from "react";

import "./App.scss";
import Popup from "./components/Popup";
import Modal from "./components/widgetModal";
import AppContext from "./context";
import variantsData from "./JSON/variants.json";
import mapData from "./JSON/map.json";

const cur = document.currentScript;
var cur_src = cur.src;
var url = new URL(cur_src);
var site = url.searchParams.get("site");
var bstype = url.searchParams.get("bstype");

window.addEventListener("resize", () => {
  let vh = window.innerHeight * 0.01;
  document.documentElement.style.setProperty("--vh", `${vh}px`);
});

function App() {
  const [dateInfo, setDateInfo] = React.useState({
    time: "1",
    date: "1",
    dayOfWeek: "1",
  });
  const [programInfo, setProgramInfo] = React.useState({
    programName: "1",
    programTime: "1",
  });
  const [addressInfo, setAddressInfo] = React.useState({
    addressOfHolding: "",
    addressLAT: "",
    addressLON: "",
  });
  let mapOBJ = Object.assign(
    ...mapData.map((obj) => {
      return { ...obj };
    })
  );
  const [clientInfo, setClientInfo] = React.useState({
    Name: "",
    Phone: "",
    EMail: "",
    WhatsAppBool: true,
  });
  const [modal, setModal] = React.useState(false);
  const [step, setStep] = React.useState("");
  const [reservInfo, setReserveInfo] = React.useState([]);
  const [total, setTotal] = React.useState(0);
  const [variants, setVariants] = React.useState([]);
  const [contactMap, setContactMap] = React.useState(false);
  const [certificateDesign, setCertificateDesign] = React.useState(false);
  const [faqScreen, setFaqScreen] = React.useState(false);
  const [congratulation, setCongratulation] = React.useState("");
  const [screen, setScreen] = React.useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });

  const [clientAgree, setClientAgree] = React.useState(false);
  console.log(site, bstype);
  console.log(step);

  React.useEffect(() => {
    setScreen({ width: window.innerWidth, height: window.innerHeight });
  }, []);
  console.log(screen);

  React.useEffect(() => {
    setReserveInfo({
      clientName: clientInfo.Name,
      clientPhone: clientInfo.Phone,
      clientEMail: clientInfo.EMail,
      clientWhatsAppBool: clientInfo.WhatsAppBool,
      reserveTime: dateInfo.time,
      reserveDate: dateInfo.date,
      reserveDay: dateInfo.dayOfWeek,
      programName: programInfo.programName,
      programTime: programInfo.programTime,
      addressOfHolding: addressInfo.addressOfHolding,
      addressLAT: addressInfo.addressLAT,
      addressLON: addressInfo.addressLON,
      giftAddressOfHolding: mapOBJ.INFO,
      giftAddressLAT: mapOBJ.LAT,
      giftAddressLON: mapOBJ.LON,
      variants: variants,
      congratulation: congratulation,
    });
  }, [
    dateInfo,
    programInfo,
    addressInfo,
    variants,
    clientInfo,
    congratulation,
    mapOBJ.INFO,
    mapOBJ.LAT,
    mapOBJ.LON,
  ]);

  React.useEffect(() => {
    setVariants(
      variantsData.map((item) => {
        return {
          id: item.id,
          price: item.price,
          quantity: 0,
          title: item.title,
          src: item.src,
          min: item.min,
          max: item.max,
        };
      })
    );
  }, []);

  return (
    <AppContext.Provider
      value={{
        modal,
        setModal,
        step,
        setStep,
        dateInfo,
        setDateInfo,
        programInfo,
        setProgramInfo,
        reservInfo,
        addressInfo,
        setAddressInfo,
        total,
        setTotal,
        variants,
        setVariants,
        contactMap,
        setContactMap,
        clientInfo,
        setClientInfo,
        certificateDesign,
        setCertificateDesign,
        faqScreen,
        setFaqScreen,
        clientAgree,
        setClientAgree,
        congratulation,
        setCongratulation,
        screen,
        setScreen,
      }}
    >
      <div className="bookpay_widget">
        <Popup />
        <Modal />
      </div>
    </AppContext.Provider>
  );
}

export default App;
